import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {Article} from "../weather.component";
import {DomSanitizer} from "@angular/platform-browser";
import {WeatherService} from "../weather.service";

@Component({
  selector: 'app-new-details',
  templateUrl: './new-details.component.html',
  styleUrls: ['./new-details.component.scss']
})
export class NewDetailsComponent implements OnInit {
  url: any
  constructor(@Inject(MAT_DIALOG_DATA) data: Article, private sanitizer: DomSanitizer, private weatherService: WeatherService) {
    this.url = this.sanitizer.bypassSecurityTrustResourceUrl(data.url)
  }

  ngOnInit(): void {
    this.weatherService.fetch().subscribe()
  }

}
